<template>
    <div>
    <el-card style="height:300px;width: 90%;margin: 0 auto">
        <quill-editor
                v-model="content"
                ref="myTextEditor"
                :options="editorOption"
                @change="onChange"
                style="height:100%;width:95%;margin: 0 auto"
        >
            <!-- toolar -->
            <div id="toolbar" slot="toolbar">
                <!-- Add a bold button -->
                <button class="ql-bold" title="">Bold</button>
                <button class="ql-italic" title="">Italic</button>
                <button class="ql-underline" title="">underline</button>
                <button class="ql-strike" title="">strike</button>
                <button class="ql-blockquote" title=""></button>
                <button class="ql-code-block" title=""></button>
                <button class="ql-header" value="1" title="1"></button>
                <button class="ql-header" value="2" title="2"></button>
                <!--Add list -->
                <button class="ql-list" value="ordered" title=""></button>
                <button class="ql-list" value="bullet" title=""></button>
                <!-- Add font size dropdown -->
                <select class="ql-header" title="">
                    <option selected></option>
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                    <option value="4">4</option>
                    <option value="5">5</option>
                    <option value="6">6</option>
                </select>
                <select class="ql-size" title="">
                    <option value="10px">10px</option>
                    <option value="12px">12px</option>
                    <option value="14px">14px</option>
                    <option value="16px" selected>16px</option>
                    <option value="18px">18px</option>
                    <option value="20px">20px</option>
                </select>
                <select class="ql-font" title="">
                    <option value="SimSun"></option>
                    <option value="SimHei"></option>
                    <option value="Microsoft-YaHei"></option>
                    <option value="KaiTi"></option>
                    <option value="FangSong"></option>
                    <option value="Arial">Arial</option>
                </select>
                <!-- Add subscript and superscript buttons -->
                <select class="ql-color" value="color" title=""></select>
                <select class="ql-background" value="background" title=""></select>
                <select class="ql-align" value="align" title=""></select>
                <button class="ql-clean" title=""></button>
                <!-- You can also add your own -->
                <span class="ql-formats">
          <button type="button" @click="imgClick">
            <svg viewBox="0 0 18 18">
                <rect class="ql-stroke" height="10" width="12" x="3" y="4"></rect>
                <circle class="ql-fill" cx="6" cy="7" r="1"></circle>
                <polyline class="ql-even ql-fill" points="5 12 5 11 7 9 8 10 11 7 13 9 13 12 5 12"></polyline>
            </svg>
          </button>
      </span>
                <span class="ql-formats">
           <button type="button" class="ql-video"></button>
       </span>
            </div>

        </quill-editor>

    </el-card>

        <crop-upload
                v-model="showCrop"
                :width="width"
                :height="height"
                :fileName="fileName"
                :uploadUrl="uploadUrl"
                @uploadSuccess="onUploadSuccess"
        ></crop-upload>

    </div>

</template>

<script>
    
    import {quillEditor} from 'vue-quill-editor'
    import CropUpload from './CropUpload';

    export default {
        props: {
            /**/
            value: {
                type: String
            },
            /**/
            uploadUrl: {
                type: String
            },
            /*filename*/
            fileName: {
                type: String
            },
            /**/
            maxSize:{
                type:Number,
                default:400//kb
            },
            /**/
            canCrop:{
                type:Boolean,
                default:false
            },
            /**/
            width:{
                type:Number,
                default:200
            },
            /**/
            height:{
                type:Number,
                default:200
            }
        },
        data() {
            return {
                content: '',
                editorOption: {
                    modules: {
                        toolbar: '#toolbar'
                    }
                },
                /**/
                showCrop:false
            }
        },
        methods: {
            onChange(){
                this.$emit('input', this.content)
            },
            /**/
            onFileChange(e){
                var fileInput = e.target;
                if (fileInput.files.length == 0) {
                    return;
                }
                if (fileInput.files[0].size > 1024 * 1024 * this.maxSize) {
                    alert('');
                    return;
                }
                if (!this.uploadUrl) {
                    console.log('no editor uploadUrl');
                    return;
                }
                var self = this;
                var data = new FormData;
                data.append(this.fileName, fileInput.files[0],name);
                this.editor.focus();
                var xhr=new XMLHttpRequest();
                xhr.open('post',this.uploadUrl);
                xhr.responseType='json';
                xhr.send(data);
                xhr.onload=function () {
                    if(xhr.status==200){
                        self.editor.insertEmbed(self.editor.getSelection().index, 'image', xhr.response.url);
                    }
                }
            },
            /* res*/
            onUploadSuccess:function (res) {
                this.editor.focus();
                this.editor.insertEmbed(this.editor.getSelection().index, 'image', res.url);
            },
            /**/
            imgClick() {
                if(this.canCrop){
                    this.showCrop=true;
                }else{
                    /*input file */
                    var input = document.createElement('input');
                    input.type = 'file';
                    input.name = this.fileName;
                    input.accept = 'image/jpeg,image/png,image/jpg,image/gif';
                    input.onchange = this.onFileChange;
                    input.click();
                }
            },

        },
        computed: {
            editor() {
                return this.$refs.myTextEditor.quill;
            }
        },
        components: {
            quillEditor,
            CropUpload
        },
        mounted(){
            this.content = this.value;
        },
        watch: {
            'value'(newVal, oldVal) {
                if (this.editor) {
                    if (newVal !== this.content) {
                        this.content = newVal
                    }
                }
            },
        }
    }
</script>

<style scoped>

</style>