import Vue from 'vue'
import App from './App.vue'
import ElementUI from 'element-ui'
import VueRouter from 'vue-router'
import router from './router/index'
import store from './store/index' //
import axios from 'axios/index'   //AxiospromiseHTTPgetpost
import validators from './assets/js/validators' //


// 
import 'element-ui/lib/theme-chalk/index.css'
import './assets/css/index.css'

//quill-editor
import  VueQuillEditor from 'vue-quill-editor'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

//Vue.use
Vue.use(VueQuillEditor)
Vue.use(ElementUI);
Vue.use(VueRouter)

// API
Vue.prototype.$axios = axios

//
Vue.prototype.validators = validators
Vue.config.productionTip = false


// url
// axiosURLapi
axios.defaults.baseURL= 'http://localhost:9000/api'
axios.defaults.headers.post['Content-Type'] = "application/json"
axios.defaults.withCredentials = true   ///
axios.defaults.headers.common['Authorization'] = store.state.token


// token
// tokentokentoken
// token
// 

//
//token
axios.interceptors.request.use(
    config =>{
        if(store.state.token){// tokenhttp headertoken
            config.headers.Authorization = 'token ${store.state.token}';
        }
        return config;
    },
    error =>{
        //
        return Promise.reject(error);
    })

//http reponse
axios.interceptors.response.use(
    response =>{
        return response;
    },
    error=>{
        if(error.response){
            switch(error.response.status){  //401
                case 401:
                    localStorage.removeItem('token');//token
                    router.replace({
                        path: '/login',
                        query: {redirect: router.currentRoute.fullPath}//
                    });
                case 403:
                    alert("403")
                    localStorage.removeItem('token');//token
                    router.replace({
                        path: '/login',
                        query: {redirect: router.currentRoute.fullPath}//
                    })
            }
        }
        return Promise.reject(error.response.data)   // 
    });


// 
router.beforeEach((to, from, next) => {

    store.state.token = sessionStorage.getItem('token');//token

    if (to.meta.requireAuth) {  // 
        if (store.state.token !== "") {  // vuex statetoken
            next();
        }
        else {
            next({
                path: '/home',
                query: {redirect: to.fullPath}  // path
            })
        }
    }
    else {
        next();
    }
})


//vue
new Vue({
  router,
  store,
  //Vuestorestore
  render: h => h(App),
}).$mount('#app')
