<template>
    <div class="HomeBox">
        <el-container>
            <Aside/>
            <el-container>
                <el-header>
                   <Breadcrumb/>
                </el-header>
                    <router-view/>
            </el-container>
        </el-container>
    </div>
</template>

<style>

</style>

<script>
    import '@/assets/css/index.css'
    import Aside from "./Aside";
    import Modify from "../user/userModify";
    import Breadcrumb from "./Breadcrumb";

    export default {
        name: 'Home',
        components: {Breadcrumb, Modify, Aside},
        data() {
            return {
                breadList: []
            }
        },
        watch: {

            //
            $route() {
                this.getBreadcrumb();
            }
        },
        methods: {

            // 
            getBreadcrumb() {
                let that = this;

                //12
                let breadNumber = typeof (this.$route.meta.breadNumber) !== 'undefined' ?
                    this.$route.meta.breadNumber : 1;

                //
                let newBread = {name: this.$route.name, path: this.$route.fullPath};

                //
                let vuexBreadList = [];

                //Number1vuex
                if (breadNumber !== 1) {

                    // breadlist
                    vuexBreadList = that.$store.state.breadListState;
                }

                if (breadNumber < vuexBreadList.length) {

                    //      breadNumbervuexBreadList
                    vuexBreadList.splice(breadNumber - vuexBreadList.length, vuexBreadList.length - breadNumber);
                }

                if (breadNumber > vuexBreadList.length) {

                    //      breadNumbervuexBreadList
                    vuexBreadList.push(newBread);
                }

                //vuex
                that.$store.commit('breadListMutations', vuexBreadList);

                //
                that.breadList = vuexBreadList;
            },
            breadcrumbClick(item) {
                this.$router.push({
                    path: item.path
                })
            }
        }

    };
</script>