//

var checkAge = (rule, value, callback) => {
    if (!value) {
        return callback(new Error(''));
    }
    setTimeout(() => {
        if (!Number.isInteger(value)) {
            callback(new Error(''));
        } else {
            if (value < 18) {
                callback(new Error('18'));
            } else {
                callback();
            }
        }
    }, 1000);
};


// 
var validateuserName = (rule, value, callback) => {
    if (value === '') {
        callback(new Error(''));
    } else {
        callback();
    }
};

// 
var validateLoginPass = (rule, value, callback) => {
    if (value === '') {
        callback(new Error(''));
    } else {
        callback();
    }
};


// 
var validatePass = (rule, value, callback) => {
    if (value === '') {
        callback(new Error(''));
    } else {
        if (this.regForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
        }
        callback();
    }
};

//
var validatePass2 = (rule, value, callback) => {
    if (value === '') {
        callback(new Error(''));
    } else if (value !== this.regForm.pass) {
        callback(new Error('!'));
    } else {
        callback();
    }
};



//
export default {
    checkAge,
    validatePass,
    validateLoginPass,
    validatePass2,
    validateuserName

}